 <template>
  <div>
    <div class="top">
      <!-- vue2 -->
      <div class="button">
        <div class="new-div" @click="handleReset">清空</div>
        <div class="new-div" @click="handleGenerate">图片</div>
        <div class="new-div" @click="gotoInsuranceSynthetic">提交</div>
      </div>
      <div class="esign">
        <div class="esign-bord">
          <vue-esign
            ref="esign"
            :width="clientWidth"
            :height="clientHeight"
            :isCrop="isCrop"
            :lineWidth="lineWidth"
            :lineColor="lineColor"
            :bgColor.sync="bgColor"
          />
        </div>
        <!-- isClearBgColor为false时，不必再给bgColor加sync修饰符或v-model -->
      </div>
    </div>
    <div>
      <img :src="resultImg" alt="" />
    </div>
  </div>
</template>
 
 <script>
import Vue from "vue";
import { Toast } from "vant";

Vue.use(Toast);
export default {
  name: "",
  components: {},

  data() {
    return {
      lineWidth: 6,
      lineColor: "#000000",
      bgColor: "",
      resultImg: "",
      isCrop: false,
      clientHeight: document.documentElement.clientHeight + 100,
      clientWidth: document.documentElement.clientWidth,
    };
  },
  mounted() {
    console.log(document.documentElement.clientWidth);
    console.log(document.documentElement.clientHeight);
  },
  methods: {
    gotoInsuranceSynthetic() {
      this.handleGenerate();
      this.$nextTick(() => {
        if (this.resultImg) {
          this.$router.push({
            path: "/insuranceSynthetic",
          });
        } else {
          Toast.fail("请签名");
        }
      });
    },
    handleReset() {
      this.$refs.esign.reset();
      this.resultImg = "";
    },

    handleGenerate() {
      this.$refs.esign
        .generate()
        .then((res) => {
          this.resultImg = res;
          console.log(res);
        })
        .catch((err) => {
          //   alert(err); // 画布没有签字时会执行这里 'Not Signned'
          Toast.fail("请签名");
        });
    },
  },
};
</script>
 
 <style lang='less' scoped>
.top {
  height: 100%;
  display: flex;
}
img {
  width: 100%;
}
.button {
  margin-right: 10px;
  display: flex;
  flex-direction: column;

  justify-content: flex-end;
}

.esign-bord {
  border: 1px #000 solid;
}
:deep(canvas) {
  max-width: 100%;
  display: block;
  width: 800px;
  height: 800.4px;
}
.new-div {
  background: #1989fa;
  margin-top: 50px;
  height: 50px;
  color: #fff;
  transform: rotate(90deg);
  line-height: 50px;
  text-align: center;
  width: 70px;
  border-radius: 15px;
  font-weight: 550;
}
</style>
 